import React from 'react';
import Carousel from 'react-material-ui-carousel';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import { Box, Button } from '@mui/material';
import '../Styles/HomeStyles.css'
const styles = {
    root: {
        position: 'relative',
        minHeight: '300px',
        display:'flex',
        justifyItems:'centre',
        },
      image: {
        width: '100%',
        height: '500px',
      },
      text: {
       fontSize:'50px',
       fontFamily: 'fantasy',
      },
      subtext: {
        fontSize:'20px',
        fontWeight:'bold',
        
       },
      textOverlay: {
        position: 'absolute',
        top: '50%',
        left: '40%',
        transform: 'translate(-50%, -50%)',
        color: 'white',
        textAlign: 'left',
        width: '800px',
        Height :'5px',
        
        

      },
      textOverlay2: {
        position: 'absolute',
        top: '60%',
        left: '40%',
        transform: 'translate(-200%, -50%)',
        color: 'white',
        textAlign: 'left',
        width: '300px',
        Height :'5px',
        
        

      },
};

const items = [
  {
    text :'Engineered solutions that drive industries forward',
    subtext: 'Specialized in Air Compressors, Heat Exchangers, AODD Pump ,Lobe Pumps, Metering Pump, Specialization in Energy saving',
    imgSrc: 'banner-img.webp',
  }
  
];

function CarouselTwo() {
  return (
    <Carousel>
    {items.map((item, index) => (
      <Card key={index} style={styles.root}>
        <img src={item.imgSrc} alt={item.text} style={styles.image} />
        <CardContent >
            <Box style={styles.textOverlay}> 
            <Typography  variant="h3" style={styles.text} >
              {item.text}
            </Typography>
            <Typography  variant="h3" style={styles.subtext} sx={{paddingTop:'10px'}} >
              {item.subtext}
            </Typography>
            <Button size='large' color="primary" variant='contained' sx={{marginTop:'60px',fontWeight:'bold'}}>Contact Us</Button>
            </Box>
          
         
        </CardContent>
      </Card>
    ))}
  </Carousel>
);
}

export default CarouselTwo               